import { Component, OnInit } from '@angular/core';


import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-gl-reports',
  templateUrl: './gl-reports.component.html',
  styleUrls: ['./gl-reports.component.scss']
})
export class GlReportsComponent implements OnInit {

  glReportsDataList:any;
  reports:any;

  isDataLoaded:boolean = false;
  isTrialGenerated:boolean = true;

  constructor(
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }


  getDateFormat(date){
  	return moment(date).format("MM-DD-YYYY");
  }

  submitGlTrialBalanceForm(form){

	this.isDataLoaded = false;
  	this.isTrialGenerated = false;

  	let details = {
  		FromDate: this.getDateFormat(this.reports.fromDate),
  		ToDate: this.getDateFormat(this.reports.toDate),
  		ApartmentId: parseInt(this.cookieService.get('apartmentId'))
  	}

  	this.accountsService.getTrialBalanceByDate(details).subscribe((res:any) => {

  		this.isDataLoaded = true;
  		this.isTrialGenerated = true;

  		this.glReportsDataList = res;

  	}, error => {
        this.isDataLoaded = true;
  		this.isTrialGenerated = true;
    });

  }

  ngOnInit() {

  	this.reports = {};

  }

}
